.position-fixed {
  width: 100%;
  height: 100vh;
}

.particles-wrapper {
  position: absolute;
  z-index: -1;
}

.home-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.home-content {
  display: flex;
  flex-direction: column;
  width: 40%;
  height: 25%;
  text-align: center;
  justify-content: center;
  background-color: var(--light-fade-bg);
}

.home-content h1 {
  color: #cff9fc;
}

.home-content h3 {
  color: #cff9fc;
}

.white-logo-img {
  width: 5rem;
}

@media screen and (max-width:850px) {
  .home-content {
    width: 70%;
  }
}

@media screen and (max-width:600px) {
  .home-content {
    width: 90%;
  }
}